<template>
  <div>
    <tarjeta-credito :trans="translations" 
                    @change="guardar_info_tarjeta"
                    @cardChanged="guarda_tipo"></tarjeta-credito>
    <div v-show="error" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <div class="alert alert-warning">
          Datos de tarjeta incorrectos
        </div>
      </div>
      <div class="col-md-4"></div>
    </div>
    <div v-show="loading" class="row">
        <div class="col-md-5"></div>
        <div class="col-md-2">
          <!--<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto;display: block;" width="100px" height="100px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
            <circle cx="50" cy="50" fill="none" stroke="#00a950" stroke-width="3" r="38" stroke-dasharray="179.070781254618261.690260418206066">
              <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"><animateTransform/>
            <circle/>
          <svg/>-->
          <svg xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.0" width="64px" height="64px" viewBox="0 0 128 128" xml:space="preserve"><path fill="#1a8600" d="M64.4 16a49 49 0 0 0-50 48 51 51 0 0 0 50 52.2 53 53 0 0 0 54-52c-.7-48-45-55.7-45-55.7s45.3 3.8 49 55.6c.8 32-24.8 59.5-58 60.2-33 .8-61.4-25.7-62-60C1.3 29.8 28.8.6 64.3 0c0 0 8.5 0 8.7 8.4 0 8-8.6 7.6-8.6 7.6z"><animateTransform attributeName="transform" type="rotate" from="0 64 64" to="360 64 64" dur="1000ms" repeatCount="indefinite"></animateTransform></path></svg>
        </div>
        <div class="col-md-5"></div>
    </div>
    <div v-show="resultado==1" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <div class="alert alert-success">
          El pago se realizó exitosamente
        </div>
      </div>
      <div class="col-md-4"></div>
    </div>
    <div v-show="resultado==2" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <div class="alert alert-danger">
          Error en el pago
        </div>
      </div>
      <div class="col-md-4"></div>
    </div>
    <div v-show="boton_visible" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <button type="button" @click="pagar" class="btn btn-success">Pagar</button>
      </div>
      <div class="col-md-4"></div>
    </div>
  </div>
</template>

<script>
  import VCreditCard from 'v-credit-card';
  import 'v-credit-card/dist/VCreditCard.css';
  import axios from 'axios';
  export default{
      data(){
        return {
          translations:{
            name: {
                label: 'Nombre del tarjeta habiente',
                placeholder: 'Nombre completo'
            },
            card: {
                label: 'Número de tarjeta',
                placeholder: 'Número de tarjeta'
            },
            expiration: {
                label: 'Expiration'
            },
            security: {
                label: 'Código de seguridad',
                placeholder: 'Código'
            }
          }

          ,card:{
            name: '',
            cardNumber: '',
            expiration: '',
            security: '',
            tipo: ''
          }

          ,stripe_key:llave_publica
          ,error:false
          ,token_pago:''
          ,idservicio:servicio
          ,token:laravel_token
          ,url:url_pago
          ,loading:false
          ,pago_terminado:false
          ,resultado:0
        }
      }
      ,computed:{
        boton_visible:function(){
          var bandera=true;
          if(this.loading)
            bandera=false;

          if(this.pago_terminado)
            bandera=false

          return bandera;
        }
      }
      ,methods:{
        guardar_info_tarjeta:function(values){
            this.card.name=values.name;
            this.card.cardNumber=values.cardNumber;
            this.card.expiration=values.expiration;
            this.card.security=values.security;
        }
        ,guarda_tipo:function(tipo){
            this.card.tipo=tipo;
        }
        ,pagar:function(){
          this.error=false;
          
          if(this.card.name=='')
            this.error=true;

          if(this.card.cardNumber=='')
            this.error=true;

          if(this.card.expiration=='')
            this.error=true;
          
          if(this.card.security=='')
            this.error=true;

          //Si this.error es true significa que hubo un error en la captura
          if(!this.error){
              var tmp=this.card.expiration.split('/');
              var self=this;
              //Funcion para llamar al stripe y pedirle un token
              this.loading=true;
              Stripe.createToken({
                  number: this.card.cardNumber,
                  cvc: this.card.security,
                  exp_month: tmp[0],
                  exp_year: tmp[1]
              },function(status,response){
                  if (response.error){
                    alert('Error al pedir el token');
                  }
                  else{
                      //Stripe ya nos devolvió el token
                      self.token_pago=response['id'];
                      var datos = {
                                  idservicio:self.idservicio
                                  ,_token:self.token
                                  ,token_stripe:self.token_pago
                                };
                      axios.post(self.url,datos)
                           .then((result) => {
                              //Si en el servidor no hubo un fallo
                              if(result.data.status=='Error'){
                                //alert(result.data.mensaje);
                                self.resultado=2;
                              }
                              else{
                                //Si el pago procede
                                self.resultado=1;
                                
                              }
                              self.loading=false;
                              self.pago_terminado=true;
                           })
                           .catch(function(error){
                            //alert('Hubo un error en el servidor');
                            self.loading=false;
                            self.pago_terminado=true;
                            self.resultado=2;
                           })
                  }
              });
          }
        }
      }
      ,components:{
          'tarjeta-credito':VCreditCard
      }
      ,created(){
        Stripe.setPublishableKey(this.stripe_key);
      }
  }
</script>

<style></style>





